import React, { Component } from "react";
import { Form, Input, Button } from "antd";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import Request from "../service/Request";
import Storage from "../util/Storage";
import NavCustom from "../component/NavCustom";
import API from "../service/API";
import history from "../util/History";
import HeaderCustom from "../component/HeaderCustom";
import Title from "antd/lib/typography/Title";


export default class Singin extends Component {

    constructor(props){
        super(props);
        this.state ={
            token:'',
            isLoading: true,
            isAuth:false,
            authFail: false,
        }
        this.onFinish = this.onFinish.bind(this);
        this.onFinishFailed = this.onFinishFailed.bind(this);
    }

    async onFinish (values){
        const data = {
            email: values.username,
            password: values.password
        }
        const res = await Request.post('api/v1/users/signin', data);
        if(res.code && res.code == 200){
            Storage.set('accessToken', res.data.accessToken);
            Request.setAccessToken(Storage.get('accessToken'));
            
            const res2 = await Request.get('api/v1/users/currents');
            this.setState({
                authFail:false
            })

            var role = await API.getCurrentRole();
            switch(String(role)){
                case 'ROLE_ADMIN' : history.push('/admins');
                break;
                case 'ROLE_MANAGER' : history.push('/managers');
                break;
                case 'ROLE_CUSTOMER' : history.push('/');
                break;
                case 'ROLE_SHIPPER' : history.push('/shippers');
                break;
                default : history.push('/shippers');
            }
        }
        else{
            this.onFinishFailed();
        }
      };
    
    onFinishFailed () {
        this.setState({
            authFail:true
        })
      };

    render() {
        const {authFail} = this.state;
        const authFailMess = authFail ? (<Form.Item style={{marginBottom:0}}><p style={{color:'red', textAlign:"center"}}>Email hoc mt khu cha chnh xc</p></Form.Item>): '';
        return (
            <div>
                <HeaderCustom navbar ={<NavCustom />} />
            <div id= 'components-form-demo-normal-login' >
                <Form
                    name="normal_login"
                    className="login-form"
                    initialValues={{ remember: true }}
                    onFinish={this.onFinish}
                    onFinishFailed={this.onFinishFailed}
                >
                    <Form.Item>
                    <Title level = {3} style={{textAlign:"center", marginTop:5,color:"#414141"}}>ng nhp</Title>
                    </Form.Item>
                    <Form.Item
                        name="username"
                        className="mt-24"
                        rules={[{ required: true, message: 'Vui lng nhp email!' }]}
                    >
                        <Input tabIndex = {1} prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Email" />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}
                    >
                        <Input tabIndex={2}
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"
                            placeholder="Mt khu"
                        />
                    </Form.Item>
                    {authFailMess}
                    {/* <Form.Item>
                        <a className="login-form-forgot" href="">
                            Forgot password
        </a>
                    </Form.Item> */}

                    <Form.Item>
                        <Button tabIndex ={3} type="primary" htmlType="submit" className="login-form-button">
                            ng nhp
        </Button>
        hoc <a href="">ng k!</a>
                    </Form.Item>
                </Form>
            </div>
            </div>
           
        )
    }
}